@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');;
body{
    font-family: "poppins",sans-serif;
    margin : 0;
    padding: 0;  
    box-sizing: border-box;
}

/*Top Bar Section*/
.topBarSection{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 100px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
    position: fixed;
    width: 90%;
    min-width: 80%;
    top: 0;
    left: 0;
    z-index: 100;
    background-color: white;
    
}

.searchBar {
    width: 100%;
    max-width: 400px;
    display: flex;
  }

  .input1 {
    width: 100%;
    padding: 10px 40px 10px 10px; 
    border: 1px solid #ccc;
    border-radius: 8px;
    font-size: 15px;
    outline: none;
    box-sizing: border-box;
  }

 


.companyTitle h2{
    color: orangered;
    text-decoration: none;
}

/*Item Section*/

.itemSection{
    display: flex;
    width:100%;
    margin-top: 100px;

}

.gallery{
    width:150px;
   
}

.gallery img{
    width: 100%;

}

/*Landing Page*/

.landingSection{
    padding: 0 120px;
}

/*Chain Section*/

.chainSection{
    
    display: flex;
    overflow-x: hidden;
    position: relative;
    height: auto;
}

.vendorBox{
    
    border-radius: 10px;
}

.vendorBox img{
    width: 350px;
    height: 250px;
    flex: 0 0 auto;
    margin-right: 30px;
    object-fit: cover;
    border-radius: 10px;

}

.btnSection{
    display: flex;
    justify-content: flex-end;
    position: relative;
    top: 50px;
    right: 10px;
    gap: 10px;
}

.btnSection button{
    border: none;
    background: none;
    cursor: pointer;
}
.btnIcons{
    font-size: x-large;
}

/*Firm Section*/

.firmSection{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap:20px;
    margin : 0 auto;
}

.firmImage{
   position: relative;
}

.firmImage img{
    width: 240px;
    height: 180px;
    flex: 0 0 auto;
    margin-right: 30px;
    object-fit: cover;
    border-radius: 10px;

}

.firmDetails{
    padding-left: 10px;
}

.filterButtons{
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
}

.filterButtons Button{
    width: 10rem;
    padding-top: 5px 0;
    cursor: pointer;
    height: 30px;
    background-color: white;
    color: black;
    font-weight: bold;
    border: 1px solid black;
}

 


.firmOffer{
    width : 100%;
    max-width: 85%;
    position: absolute;
    bottom : 5px;
    padding-left: 11px;
    background-color: white;
    font-size: 1.2rem;
    font-weight: 800;
    padding-bottom: 10px;
    padding-top: 20px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    background: transparent;
    color: white;
    background: linear-gradient(to bottom, rgba(50,0,0,0.08),black);
    text-transform: uppercase;
    
}


.firmArea{
    font-size: small;
    color: gray;
}

/*product section*/
.productSection{
    display: flex;
    flex-direction: column;
    padding: 0 150px;
    margin-top: 100px;
}
.productSection h3{
    text-align: center;
    background-color: orange;
}
.productBox{
    display: flex;
    justify-content: space-between;
    padding: 10px 30px;
    border-top: 2px solid lightgray;
    position: relative;
}

.productImage img{
    width: 100px;
    border-radius: 5px;
}

.addButton{
    border: 1px solid lightgray;
    color: green;
    text-align: center;
    border-radius: 5px;
    background-color: white;
    position: absolute;
    width: 100%;
    max-width: 80px;
    bottom: 10px;
    margin-left: 10px;
    cursor: pointer;
}

.link{
    color: black;
    text-decoration: none;
}

/*loader section */

.loaderSection{
    display: flex;
    justify-content: center;
    align-items: center;
}

@media (max-width: 576px){
    .topBarSection{
        padding: 0 20px;
    }

    .landingSection{
        padding: 0 10px;
    }
    .itemSection{
        margin-top: 80px;
    }
    h3{
        font-weight: 800;
        font-size: medium;
        color: black
    }
    .chainSection{
        height: 190px;
    }

    .vendorBox img{
        width: 260px;
        height: 180px;
        flex: 0 0 auto;
        margin-right: 30px;
        object-fit: cover;
        border-radius: 10px;
    }

    .mediaChainSection{
        margin-top: -30px;
    }

    .firmSection{
        margin-left: 20px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .firmOffer{
       position: absolute;
       width: 100%;
       max-width: 87%;
    }

    .firmImage img{
        width: 300px;
       
    }

    .productSection{
      
        padding: 0 20px;
    }
    .productSection h3{
        text-align: center;
        background-color: orange; 
    }

    .filterButtons button{
        width: 5rem;
    }

}